@use "../variables/queries" as *;
@mixin xxs-screen {
  @media (max-width:$xs-screen) { @content}
}
@mixin xs-screen {
  @media (min-width:$xs-screen) { @content}
}
@mixin sm-screen {
  @media (min-width:$sm-screen) { @content}
}
@mixin md-screen {
  @media (min-width:$md-screen) { @content}
}
@mixin lg-screen {
  @media (min-width:$lg-screen) { @content}
}
@mixin xl-screen {
  @media (min-width:$xl-screen) { @content}
}